<template>
  <v-form ref="form" @submit.prevent>
    <v-container>
      <v-subheader>Cidade</v-subheader>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="cidade.id"
            label="Id"
            outlined
            disabled
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="cidade.nome"
            :counter="50"
            label="Nome"
            placeholder="Nome da Cidade"
            outlined
            required
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col class="d-flex" cols="12">
          <v-combobox
          :items="ufs"
          :hint="`${cidade.uf.sigla + ' - ' +cidade.uf.nome}`"
          v-model="cidade.uf"
          item-text="nome"
          item-value="id"
          label="UFs"
          placeholder="Escolha a UF"
          return-object
          outlined
          required></v-combobox>
        </v-col>
      </v-row>
      <v-btn color="success" class="" @click="save">Salvar</v-btn>
    </v-container>
  </v-form>
</template>

<script>
import DataService from "../service/CidadeDataService";
import UFDataService from "../service/UFDataService";

export default {
  name: "Cidades",
  data() {
      return {
          cidade: {id:0,nome:'',uf:{id:0,sigla:'',nome:''}},
          uf: {id:0,nome:''},
          ufs: []
      };
  },
  computed: {
      id() {
          return this.$route.params.id;
      }
  },
  methods: {
      //textCombobox: uf => uf.sigla + ' â€” ' + uf.nome,
      refresh() {
          if(this.id > 0){
              DataService.retrieve(this.id)
                  .then(response => {
                  this.cidade = response.data
              });
          }
          UFDataService.retrieveAll()
            .then(response => {
            this.ufs = response.data
          })
      },
      save(e) {
          e.preventDefault();
          if(this.id > 0){
              DataService.update(this.id, this.cidade, {
                  id: this.id,
                  cidade: this.cidade
              }).then(() => {
                  console.log(this.cidade)
                  this.$router.push('/cidades')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          } else {
              console.log(this.cidade);
              DataService.create(this.cidade, {
                  cidade: this.cidade
              }).then(() => {
                  console.log(this.cidade)
                  this.$router.push('/cidades')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          }
      }
  },
  created() {
      this.refresh()
  }
};
</script>

